import React, { Component } from 'react';
import './List.css';

export class List extends Component {

    render() {
        const list = this.props.list;
        const showContent = this.props.showContent;
        const content1 = list.map(
            content => {
                while (content.id <= 25) {
                    if (content.id < 10) {
                        return (
                            <div className="content" key={content.id}>
                                <form onClick={() => { showContent(content.id, content.content) }}>
                                    <div><span className="index">{content.id}. &nbsp;</span> {content.content}</div>
                                </form>
                            </div>
                        )
                    }
                    else {
                        return (
                            <div className="content" key={content.id}>
                                <form onClick={() => { showContent(content.id, content.content) }}>
                                    <div><span className="index">{content.id}.</span> {content.content}</div>
                                </form>
                            </div>
                        )
                    }

                }
            })
        const content2 = list.map(
            content => {
                while (content.id > 25 && content.id <= 50) {
                    return (
                        <div className="content" key={content.id}>
                            <form onClick={() => { showContent(content.id, content.content) }}>
                                <div><span className="index">{content.id}.</span> {content.content}</div>
                            </form>
                        </div>
                    )
                }
            })
        const content3 = list.map(
            content => {
                while (content.id > 50 && content.id <= 75) {
                    return (
                        <div className="content" key={content.id}>
                            <form onClick={() => { showContent(content.id, content.content) }}>
                                <div><span className="index">{content.id}.</span> {content.content}</div>
                            </form>
                        </div>
                    )
                }
            })
        const content4 = list.map(
            content => {
                while (content.id > 75 && content.id <= 100) {
                    return (
                        <div className="content" key={content.id}>
                            <form onClick={() => { showContent(content.id, content.content) }}>
                                <div><span className="index">{content.id}.</span> {content.content}</div>
                            </form>
                        </div>
                    )
                }
            })

        return (
            <div className="list">
                <div className="content-wrapper" group="1">
                    {content1}
                </div>
                <div className="content-wrapper" group="2">
                    {content2}
                </div>
                <div className="content-wrapper" group="3">
                    {content3}
                </div>
                <div className="content-wrapper" group="4">
                    {content4}
                </div>
            </div>
        )

    }
}

export default List






